<template>
    <div class="summary-box">
        <div class="box-info">
            <div class="info-img">陈前</div>
            <div class="info-title">
                <div class="title-sex">性别：男</div>
                <div class="title-title">一个前端工程师</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "home-summary",
  data() {
    return {};
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.summary-box{
    width: 100%;
    height: 100%;
    .box-info {
        display: flex;
        padding: 20px 0;
        height: 100px;
        justify-content: space-around;
        .info-img {
            width: 100px;
            background-color: yellow;
            border-radius: 50%;
            line-height: 100px;
            text-align: center;
            color: #fff;
        }
        .info-title {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }
    }
}
</style>




